<template>
  <div class="card">
    <div class="row">
      <div class="card-header col-md-8 ">
        <h4>Blokaj Tablosu</h4>
      </div>
      <div class="card-header col-md-4">
        UBE SENZ
        <multiselect v-model="tableValue"
          :options="$store.getters.GetBranchInfo.map(t => { return { 'name': t.BranchName, 'id': t.id } })"
          :searchable="false" :group-select="true" :close-on-select="false" track-by="name" label="name"
          :select-label="value" @change="getRoomData" placeholder="Seiniz"></multiselect>
      </div>
    </div>
  </div>
  
  <div>
    <div class="card">
    <div class="gstc-wrapper" ref="gstcElement"></div>
    </div>
  </div>
</template>

<script>
import GSTC from "gantt-schedule-timeline-calendar/dist/gstc.wasm.esm.min.js";
import { Plugin as TimelinePointer } from "gantt-schedule-timeline-calendar/dist/plugins/timeline-pointer.esm.min.js";
import { Plugin as Selection } from "gantt-schedule-timeline-calendar/dist/plugins/selection.esm.min.js";
import { Plugin as ItemResizing } from "gantt-schedule-timeline-calendar/dist/plugins/item-resizing.esm.min.js";
import { Plugin as ItemMovement } from "gantt-schedule-timeline-calendar/dist/plugins/item-movement.esm.min.js";
import { Plugin as Bookmarks } from "gantt-schedule-timeline-calendar/dist/plugins/time-bookmarks.esm.min.js";
import { Plugin as CalendarScroll } from "gantt-schedule-timeline-calendar/dist/plugins/calendar-scroll.esm.min.js";
import "gantt-schedule-timeline-calendar/dist/style.css";
import { ref, onMounted, onBeforeUnmount } from "vue";

// helper functions

function generateRows() {
  /**
   * @type { import("gantt-schedule-timeline-calendar").Rows }
   */
  const rows = {};
  for (let i = 0; i < 100; i++) {
    const id = GSTC.api.GSTCID(i.toString());
    rows[id] = {
      id,
      label: `Oda ${i}`,
    };
  }
  return rows;
}

function generateItems() {
  /**
   * @type { import("gantt-schedule-timeline-calendar").Items }
   */
  const currentDate=new Date();
  const items = {};
  let start = GSTC.api.date().startOf("day").subtract(6, "day");
  for (let i = 0; i < 30; i++) {
    const id = GSTC.api.GSTCID(i.toString());
    const rowId = GSTC.api.GSTCID(Math.floor(Math.random() * 100).toString());
    start = start.add(1, "day");
    items[id] = {
      id,
      label: `Mehmet KARAARSLAN ${i}`,
      rowId,
      time: {
        start: start.valueOf(),
        end: start.add(1, "day").endOf("day").valueOf(),
      },
    };
  }
  return items;
}

// main component

export default {
  name: "GSTC",
  setup() {
    let gstc, state;
    const gstcElement = ref(null);

    onMounted(() => {
      /**
       * @type { import("gantt-schedule-timeline-calendar").Config }
       */
      const config = {
        
        scroll: {
    horizontal: {
      width: 20,
      minInnerSize: 40,
      multiplier: 3,
      precise: false,
      byPixels: false,
    },
    vertical: {
      width: 20,
      minInnerSize: 40,
      multiplier: 3,
      precise: false,
      byPixels: false,
    },
  },
        licenseKey:
        //sunucuda alan key altta ki locale
        "====BEGIN LICENSE KEY====\||U2FsdGVkX1/WD8VytVN24ItTd9wBanplOJuq6XvDxIH0mDdTmwlRSRYxS5nBQm4UOto96+q24IqNLU1YJkPpOe5PFw41UwvKcybU2yQVbaw=\\n====END LICENSE KEY====",
          // "====BEGIN LICENSE KEY====\||U2FsdGVkX1/SWWqU9YmxtM0T6Nm5mClKwqTaoF9wgZd9rNw2xs4hnY8Ilv8DZtFyNt92xym3eB6WA605N5llLm0D68EQtU9ci1rTEDopZ1ODzcqtTVSoFEloNPFSfW6LTIC9+2LSVBeeHXoLEQiLYHWihHu10Xll3KsH9iBObDACDm1PT7IV4uWvNpNeuKJc\\n====END LICENSE KEY====",
        plugins: [TimelinePointer(), Selection(), ItemResizing(), ItemMovement(), Bookmarks(),CalendarScroll()],
        list: {
          columns: {
            data: {
               
              [GSTC.api.GSTCID("label")]: {
                id: GSTC.api.GSTCID("label"),
                width: 100,
                data: "label",
                header: {
                  content: "Oda No",
                },
              },
            },
          },
          rows: generateRows(),
        },
        chart: {
          items: generateItems(),
        },
        /* ... */
        locale: {
          name: 'tr',
          weekdays: 'Pazar_Pazartesi_Sal_aramba_Perembe_Cuma_Cumartesi'.split('_'),
          weekdaysShort: 'Paz_Pts_Sal_ar_Per_Cum_Cts'.split('_'),
          weekdaysMin: 'Pz_Pt_Sa_a_Pe_Cu_Ct'.split('_'),
          months: 'Ocak_ubat_Mart_Nisan_Mays_Haziran_Temmuz_Austos_Eyll_Ekim_Kasm_Aralk'.split('_'),
          monthsShort: 'Oca_ub_Mar_Nis_May_Haz_Tem_Au_Eyl_Eki_Kas_Ara'.split('_'),
          weekStart: 1,
          formats: {
            LT: 'HH:mm',
            LTS: 'HH:mm:ss',
            L: 'DD.MM.YYYY',
            LL: 'D MMMM YYYY',
            LLL: 'D MMMM YYYY HH:mm',
            LLLL: 'dddd, D MMMM YYYY HH:mm'
          },
          relativeTime: {
            future: '%s sonra',
            past: '%s nce',
            s: 'birka saniye',
            m: 'bir dakika',
            mm: '%d dakika',
            h: 'bir saat',
            hh: '%d saat',
            d: 'bir gn',
            dd: '%d gn',
            M: 'bir ay',
            MM: '%d ay',
            y: 'bir yl',
            yy: '%d yl'
          },
          ordinal: n => `${n}.`
        }
        /* ... */
      };
      const calendarScrollOptions = {
  enabled: true,
};
      state = GSTC.api.stateFromConfig(config);
      globalThis.state = state;
      gstc = GSTC({
        element: gstcElement.value,
        state,
      });
      globalThis.gstc = gstc;
    });

    onBeforeUnmount(() => {
      if (gstc) gstc.destroy();
    });

    function updateFirstRow() {
      state.update(`config.list.rows.${GSTC.api.GSTCID("0")}`, (row) => {
        row.label = "Changed dynamically";
        return row;
      });
    }

    function changeZoomLevel() {
      state.update("config.chart.time.zoom", 21);
    }

    return {
      gstcElement,
 
    };
  },

};
</script>
<style scoped>
.gstc-component {
  margin: 0;
  padding: 0;
}

.toolbox {
  padding: 10px;
}
</style>
